import styled, { CSSObject } from "styled-components";
import { Orthogonal } from "../icons";
import { IOrthogonalTitle } from "./logo.d";

export const OrthogonalLogo = styled(Orthogonal)({});

export const OrthogonalTitle = ({ children, ...props }: IOrthogonalTitle) => {
  return (
    <TitleWrapper {...props}>
      <OrthogonalLogo />
      {children}
    </TitleWrapper>
  );
};

const TitleWrapper = styled.div<IOrthogonalTitle>(
  ({ size, theme }) =>
    ({
      color: theme.colors.primary,
      display: "flex",
      alignItems: "baseline",
      fontSize: 20,
      whiteSpace: "nowrap",
      svg: {
        width: size === "large" ? 300 : "auto",
        height: size === "small" ? 40 : "auto",
        bottom: size === "large" ? "-0.8rem" : "-0.6rem",
        position: "relative",
        marginRight: "13px",
      },
      userSelect: "none",
    } as CSSObject)
);
